.restaurants {
  grid-gap: 1em;
  grid-template-columns: repeat(auto-fill, min(400px, 90vw));
  grid-auto-rows: 200px;
  align-content: center;
  justify-content: center;
  margin: 2em;
  display: grid;
}

.restaurants .restaurant {
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  position: relative;
}

.restaurants .restaurant h1 {
  color: #000;
  text-align: center;
  width: 100%;
  background-color: rgba(255, 255, 255, .667);
  border-radius: 0 0 10px 10px;
  margin: 0;
  padding: .25em 0;
  font-size: 1.4rem;
  position: absolute;
  bottom: 0;
}

.restaurants .restaurant:hover {
  box-shadow: 0 0 6px 2px #0f0f0f;
}

#order-details {
  background-color: var(--background-dark);
  text-align: left;
  width: 50%;
  border-radius: 10px;
  margin: 2em auto;
  padding: 1em 2em;
  display: none;
}

#order-details h1 {
  text-align: center;
  margin: 0;
  font-size: 1.6rem;
}

#order-details p {
  margin-bottom: .5em;
}

textarea {
  border: 1px solid var(--foreground);
  color: var(--foreground);
  width: 100%;
  background: none;
  border-radius: 4px;
  margin: 1em 0;
  font-family: sans-serif;
  font-size: 1em;
}

select {
  margin: 0 .5em;
  font-size: .9em;
}

#location {
  width: 50%;
}

.buttons {
  justify-content: space-between;
  margin: 2em 0 1em;
  display: flex;
}

@media screen and (min-width: 420px) {
  .restaurant h1 {
    font-size: 1.75rem;
  }
}

